/* app/sucessfull-case/mountain-tourist-resort/caseone.module.css */

.customH1 {
  font-size: 3.5rem !important;     /* 比默认 h1 更大 */
  font-weight: 900 !important;      /* 超粗 */
  line-height: 1.1;
  margin-bottom: 2rem !important;
  background: linear-gradient(to right, #1e40af, #3b82f6);
  -webkit-background-clip: text;
  background-clip: text;
  -webkit-text-fill-color: transparent;
  /* 可选：加个蓝紫渐变色，非常高级 */
}

/* 如果你不想渐变色，就用这行纯黑超粗 */
.customH1Plain {
  font-size: 3.5rem !important;
  font-weight: 900 !important;
  color: #0f172a !important;   /* 深灰黑 */
}